<template>
	<div class="personal-content-body">
		<div class="table-content">
			<div class="clear table-content-title">
				<!-- <div class="left">周工作安排</div> -->
				<div class="right flex flex_center">
					<div class="schedule_weekbtn" :class="{'current':week==-1}" @click="changeWeek(-1,'')">上周</div>
					<div class="schedule_weekbtn" :class="{'current':week==0}" @click="changeWeek(0,'')">本周</div>
					<div class="schedule_weekbtn" :class="{'current':week==1}" @click="changeWeek(1,'')">下周</div>
					<el-date-picker size="mini" placeholder="选择日期" v-model="currentDate" type="date" :editable='false' @change="changeDate" value-format="timestamp">
					</el-date-picker>
					<div class="table-group-title-btn btn-blue" @click="editClick" v-if="!editall">编辑</div>
					<div class="table-group-title-btn btn-blue" @click="saveClick" v-if="editall">保存</div>
					<div class="schedule_icontextbtn" @click="cancelClick" v-if="editall">取消</div>
				</div>
			</div>
			<div class="table-group">
				<div class="table-group-content">
					<div class="schedule_box">
						<div class="schedule_header" :class="{'schedule_header_sz':week==-1,'schedule_header_xz':week==1,'schedule_header_bz':week==0}">
							{{weekText}}
						</div>
						<div class="schedule_group" v-for="(item,pindex) in dataList">
							<div class="schedule_group_left">
								<div class="schedule_group_left_left">
									<div class="schedule_week">{{item.weekday}}</div>
									<div class="schedule_date">{{item.date_str}}</div>
								</div>
								<div class="schedule_group_left_right">
									<div class="schedule_icontextbtn" v-if="!item.edit" @click="item.edit=true">
										<span class="iconfont icon-bianji"></span>编辑
									</div>
									<div class="table-group-title-btn btn-blue" v-if="item.edit&&!editall" @click="saveClick">
										保存
									</div>
									<div class="schedule_icontextbtn" v-if="item.edit&&!editall" @click="cancelClick">取消
									</div>
								</div>
							</div>
							<div class="schedule_group_right">
								<div class="schedule_item" v-if="item.haschild==false&&!item.edit">
									<div class="schedule_nodata">
										<img src="../../images/weeklySchedule_nodata.png">
										<div>暂无工作安排</div>
									</div>
								</div>
								<div class="" v-for="(child,index) in item.child" v-else>
									<div class="schedule_item" v-if="child.hasdelete!=true">
										<div class="schedule_notext" v-if="!item.edit&&child.reasons==''">暂无事由信息</div>
										<div class="schedule_content" v-if="!item.edit&&child.reasons!=''">{{child.reasons}}</div>
										<textarea class="schedule_content_textarea" placeholder="请输入事由..." v-if="item.edit" v-model="child.reasons"></textarea>
										<div class="schedule_info">
											<div class="schedule_info_left">
												<span class="iconfont icon-lianxiren"></span>出席人
											</div>
											<div class="schedule_info_right">
												<div class="schedule_userlist">
													<div class="schedule_notext" v-if="!item.edit&&child.attendee.length==0">暂无出席人</div>
													<div class="schedule_useritem" v-for="(attendee,attindex) in child.attendee">
														<img :src="attendee.url" @error="getImgurl">
														<div class="schedule_username">{{attendee.name}}</div>
														<span class="iconfont icon-delete-black" @click="deletePeople(attindex,child.attendee)" v-if="item.edit"></span>
													</div>
													<div class="schedule_additem">
														<span class="iconfont icon-add" v-if="item.edit" @click="changeJoin('attendee',pindex,index)"></span>
													</div>
												</div>
											</div>
										</div>
										<div class="schedule_info">
											<div class="schedule_info_left">
												<span class="iconfont icon-lianxiren"></span>参加人
											</div>
											<div class="schedule_info_right">
												<div class="schedule_userlist">
													<div class="schedule_notext" v-if="!item.edit&&child.participant.length==0">暂无参加人</div>
													<div class="schedule_useritem" v-for="(participant,parindex) in child.participant">
														<img :src="participant.url" @error="getImgurl">
														<div class="schedule_username">{{participant.name}}</div>
														<span class="iconfont icon-delete-black" @click="deletePeople(parindex,child.participant)" v-if="item.edit"></span>
													</div>
													<div class="schedule_additem">
														<span class="iconfont icon-add" @click="changeJoin('participant',pindex,index)" v-if="item.edit"></span>
													</div>
												</div>
											</div>
										</div>
										<div class="schedule_info">
											<div class="schedule_info_left">
												<span class="iconfont icon-dingwei"></span>地址
											</div>
											<div class="schedule_info_right">
												<div class="schedule_address" v-if="!item.edit&&child.place!=''">{{child.place}}</div>
												<div class="schedule_address schedule_address_notext" v-if="!item.edit&&child.place==''">暂无地址信息</div>
												<input class="schedule_content_input" placeholder="请输入地址..." v-if="item.edit" v-model="child.place">
											</div>
										</div>
										<div class="schedule_btnbox flex">
											<div class="table-group-title-btn btn-blue-border" v-if="item.edit&&item.child.leavecount!=0" @click="deleteItem(pindex,index)">
												<span class="iconfont icon-shanchu"></span>删除
											</div>
											<div class="table-group-title-btn btn-blue" @click="addItem(pindex,index)" v-if="item.edit">
												<span class="iconfont icon-jia"></span>添加
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<add-people-modal :visibleDialog="joinVisibleDialog" name="join" :selectedId="selectedIdList" @changeshow="changeModalShow" @setData="getModaldata" @getData="getFullModaldata" title="选择默认抄送人"></add-people-modal>
		
		<!--  设置权限弹窗开始-->
		<!--<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="setPowerCancel">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog>-->
		<!--设置权限弹窗结束-->
		
	</div>
</template>
<script>
	import { getWeeklyWorkList } from '../../config/api'
	import addPeopleModal from '../applyManage/addPeopleModal.vue' //右部模块
	//import fundLead from '../../pages/fundrunComponent/fundLead.vue'
	import {
		dialogConfirm,
		dialogConfirmHasbtn,
		compareJson,
		getStore
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				dataList: [],
				oldDatalist: [],
				joinVisibleDialog: false, //选择参会人弹窗
				currentDate: '', //当前日期
				week: 0, //周偏移量//当前周为0
				weekText: '本周',
				editall: false, //是否正在编辑
				selectedIdList: [],
				currentEditPeople: {
					name: '',
					pindex: 0,
					index: 0,
				},

				newitem: {
					id: 0,
					reasons: '',
					place: '',
					create_id: 0,
					lv: 0,
					attendee: [],
					participant: [],
				},
			}
		},
		components: {
			addPeopleModal
			//fundLead
		},
		created() {
			let userInfo = JSON.parse(getStore('htcx_userInfo'));
			let is_admin = userInfo.is_admin;
			this.newitem.lv = is_admin;
			this.getData();
		},
		computed: {},
		mounted() {

		},
		methods: {
			setEditall(result) {
				this.editall = result;
				this.dataList.forEach(item => {
					item.edit = result
				})
			},
			editClick() {
				this.setEditall(true);
				this.oldDatalist = JSON.parse(JSON.stringify(this.dataList))
			},
			saveClick() {
				this.setEditall(false);
				getWeeklyWorkList({
					flag: 2,
					data: this.dataList
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '修改成功');
						this.getData();
					} else {
						dialogConfirm('fail', res.data.message)
					}
				})
			},
			cancelClick() {
				this.getData();
			},
			deleteItem(pindex, index) {
				dialogConfirmHasbtn('确定要删除吗？').then(res => {
					if(this.dataList[pindex].child.leavecount == 0) {
						//dialogConfirm('info','');
						console.log(this.dataList[pindex].child.leavecount);
						return;
					}
					this.dataList[pindex].child.leavecount = this.dataList[pindex].child.leavecount - 1;
					this.dataList[pindex].child[index].hasdelete = true;
					this.dataList[pindex].child[index].reasons = '';
					this.dataList[pindex].child[index].place = '';
					this.dataList[pindex].child[index].attendee = [];
					this.dataList[pindex].child[index].participant = [];
					/*if (this.dataList[pindex].child.leavecount == 0) {
					  let newitem = JSON.parse(JSON.stringify(this.newitem))
					  this.dataList[pindex].child.push(newitem);
					}*/
				}).catch(() => {})
			},
			addItem(pindex, index) {
				let child = this.dataList[pindex].child[index];
				let empty = true;
				if(child.reasons !== '' || child.place !== '' || child.attendee.length != 0 || child.participant.length != 0) {
					empty = false;
				}
				/*if (empty) {
				  dialogConfirm('info', '您还有未填的空事件')
				} else {
				  
				}*/
				let newitem = JSON.parse(JSON.stringify(this.newitem))
				this.dataList[pindex].child.splice(index + 1, 0, newitem);
				this.dataList[pindex].child.leavecount = this.dataList[pindex].child.leavecount + 1;
			},
			//修改参与人
			changeJoin(name, pindex, index) {
				this.joinVisibleDialog = true;
				this.currentEditPeople = {
					name: name,
					pindex: pindex,
					index: index
				}
				let selectedList = this.dataList[pindex].child[index][name];
				let selectedIdList = [];
				selectedList.forEach(item => {
					selectedIdList.push(item.uid);
				})
				this.selectedIdList = selectedIdList;
			},
			getFullModaldata(list, strlist) {
				list.forEach(item => {
					item.uid = item.id;
				})
				let pindex = this.currentEditPeople.pindex
				let name = this.currentEditPeople.name
				let index = this.currentEditPeople.index
				this.dataList[pindex].child[index][name] = list;
			},
			getData() {
				this.setEditall(false);
				getWeeklyWorkList({
					flag: 1,
					offset: this.week
				}).then(res => {
					if(res.data.msgNo == 1) {
						let dl = res.data.payload;
						dl.forEach(item => {
							item.year = item.date.split('-')[0];
							item.date_str = item.date.split('-')[1] + '-' + item.date.split('-')[2];
							item.edit = false;
							if(item.child.length == 1 && item.child[0].id == 0) {
								for(let i in this.newitem) {
									item.child[0][i] = this.newitem[i]
								}
								item.haschild = false;
								item.child.leavecount = 0;
							} else {
								item.haschild = true;
								item.child.leavecount = item.child.length;
							}
						})
						this.dataList = dl;
					}
				})
			},
			changeDate(e) {
				if(e == null) {
					this.changeWeek(0, '');
					return;
				}
				let enow = new Date(e);
				let eend = new Date(e);
				let eday = enow.getDay() == 0 ? 7 : enow.getDay();
				enow.setDate(enow.getDate() - eday + 1);
				eend.setDate(eend.getDate() - eday + 1);
				eend.setDate(eend.getDate() + 6);
				let weektext = enow.Format('yyyy-MM-dd') + '~' + eend.Format('yyyy-MM-dd')

				let enowstr = enow.getTime();
				let now = new Date();
				let nowday = now.getDay() == 0 ? 7 : now.getDay();
				now.setDate(now.getDate() - nowday + 1);
				let nowstr = now.getTime();

				let week = 0;
				if(enowstr > nowstr) {
					week = parseInt((enowstr - nowstr) / 1000 / 60 / 60 / 24);
					week = week == 0 ? 0 : week + 1;
				} else {
					week = -(parseInt((nowstr - enowstr) / 1000 / 60 / 60 / 24));
					week = week == -0 ? 0 : week;
				}
				week = week / 7;
				this.changeWeek(week, weektext);

			},
			changeWeek(index, weektext) {
				this.week = index;
				switch(this.week) {
					case -1:
						this.weekText = '上周';
						break;
					case 0:
						this.weekText = '本周';
						break;
					case 1:
						this.weekText = '下周';
						break;
					default:
						this.weekText = weektext;
						break;
				}
				this.getData();
			},
			deletePeople(index, list) {
				list.splice(index, 1);
			},
			changeModalShow(type, name) {
				this[name + 'VisibleDialog'] = type
			},
			getModaldata(name, list) {

			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			},
		}
	}
</script>
